<template>
  <v-app>
    <v-main>
      <v-container fluid fill-height>
        <v-layout align-center justify-center>
          <v-flex xs12 sm8 md4>
            <v-card class="elevation-12">
              <v-toolbar dark color="primary">
                <v-toolbar-title>Login</v-toolbar-title>
              </v-toolbar>
              <v-card-text>
                <v-alert type="error" dark v-if="hasError">
                  Wrong email or password!
                </v-alert>
                <v-form>
                  <v-text-field v-model="form.email" :disabled="loading" prepend-icon="mdi-account" label="Email" type="text"></v-text-field>
                  <v-text-field v-model="form.password" :disabled="loading" prepend-icon="mdi-lock" label="Password" type="password"></v-text-field>
                </v-form>
              </v-card-text>
              <v-card-actions>
                <v-spacer></v-spacer>
                <v-btn color="primary" block @click="submit" :loading="loading" :disabled="loading">Login</v-btn>
              </v-card-actions>
              <v-divider></v-divider>
              <div style="padding: 10px;">
                Don't have an account yet? <router-link to="/register">Create an account</router-link>
              </div>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      hasError: false,
      loading: false,
      form: {
        email: null,
        password: null
      }
    }
  },
  methods: {
    async submit() {
      this.loading = true;
      this.hasError = false;
      try {
        let response = await axios.post('/api/auth/generate-token', this.form);
        this.$store.commit('login', response.data);
        this.$router.push('/');
      } catch (e) {
        this.hasError = true;
        this.loading = false;
      }
    }
  }
}
</script>